 body {
    font-family: 'Roboto', sans-serif;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-weight: 700;
  }
  
  hr.star-light,
  hr.star-dark {
    max-width: 15rem;
    padding: 0;
    text-align: center;
    border: none;
    border-top: solid 0.25rem;
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
    margin-left: auto;
    margin-right: auto;
  }
  
  hr.star-light:after,
  hr.star-dark:after {
    position: relative;
    top: -.8em;
    display: inline-block;
    padding: 0 0.25em;
    content: '\F005';
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    font-size: 2em;
  }
  
  hr.star-light {
    border-color: #fff;
  }
  
  hr.star-light:after {
    color: #fff;
    background-color: #18BC9C;
  }
  
  hr.star-dark {
    border-color: #2C3E50;
  }
  
  hr.star-dark:after {
    color: #2C3E50;
    background-color: white;
  }
  
  section {
    padding: 6rem 0;
  }
  
  section h2 {
    font-size: 2.25rem;
    line-height: 2rem;
  }
  
  @media (min-width: 992px) {
    section h2 {
      font-size: 3rem;
      line-height: 2.5rem;
    }
  }
  
  .btn-xl {
    padding: 1rem 1.75rem;
    font-size: 1.25rem;
  }
  
  .btn-social {
    width: 3.25rem;
    height: 3.25rem;
    font-size: 1.25rem;
    line-height: 2rem;
  }
  
  .scroll-to-top {
    z-index: 1042;
    right: 1rem;
    bottom: 1rem;
    display: none;
  }
  
  .scroll-to-top a {
    width: 3.5rem;
    height: 3.5rem;
    background-color: rgba(33, 37, 41, 0.5);
    line-height: 3.1rem;
  }
  
  #mainNav {
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-weight: 700;
  }
  
  #mainNav .navbar-brand {
    color: #fff;
  }
  
  #mainNav .navbar-nav {
    margin-top: 1rem;
    letter-spacing: 0.0625rem;
  }
  
  #mainNav .navbar-toggler {
    font-size: 80%;
    padding: 0.8rem;
  }
  
  @media (min-width: 992px) {
    #mainNav {
      padding-top: 1.5rem;
      padding-bottom: 1.5rem;
      transition: padding-top 0.3s, padding-bottom 0.3s;
    }
    #mainNav .navbar-brand {
      font-size: 2em;
      transition: font-size 0.3s;
    }
    #mainNav .navbar-nav {
      margin-top: 0;
    }
    #mainNav .navbar-nav > li.nav-item > a.nav-link.active {
      color: #fff;
      background: #18BC9C;
    }
    #mainNav .navbar-nav > li.nav-item > a.nav-link.active:active, #mainNav .navbar-nav > li.nav-item > a.nav-link.active:focus, #mainNav .navbar-nav > li.nav-item > a.nav-link.active:hover {
      color: #fff;
      background: #18BC9C;
    }
    #mainNav.navbar-shrink {
      padding-top: 0.5rem;
      padding-bottom: 0.5rem;
    }
    #mainNav.navbar-shrink .navbar-brand {
      font-size: 1.5em;
    }
  }
  
  header.masthead {
    padding-top: calc(6rem + 72px);
    padding-bottom: 6rem;
  }
  
  header.masthead h1 {
    font-size: 3rem;
    line-height: 3rem;
  }
  
  header.masthead h2 {
    font-size: 1.3rem;
    font-family: 'Lato';
  }
  
  @media (min-width: 992px) {
    header.masthead {
      padding-top: calc(6rem + 106px);
      padding-bottom: 6rem;
    }
    header.masthead h1 {
      font-size: 4.75em;
      line-height: 4rem;
    }
    header.masthead h2 {
      font-size: 1.75em;
    }
  }
  
  .portfolio {
    margin-bottom: -15px;
  }
  
  .portfolio .portfolio-item {
    position: relative;
    display: block;
    max-width: 25rem;
    margin-bottom: 15px;
  }
  
  .portfolio .portfolio-item .portfolio-item-caption {
    transition: all ease 0.5s;
    opacity: 0;
    background-color: rgba(24, 188, 156, 0.9);
  }
  
  .portfolio .portfolio-item .portfolio-item-caption:hover {
    opacity: 1;
  }
  
  .portfolio .portfolio-item .portfolio-item-caption .portfolio-item-caption-content {
    font-size: 1.5rem;
  }
  
  @media (min-width: 576px) {
    .portfolio {
      margin-bottom: -30px;
    }
    .portfolio .portfolio-item {
      margin-bottom: 30px;
    }
  }
  
  .portfolio-modal .portfolio-modal-dialog {
    padding: 3rem 1rem;
    min-height: calc(100vh - 2rem);
    margin: 1rem calc(1rem - 8px);
    position: relative;
    z-index: 2;
    box-shadow: 0 0 3rem 1rem rgba(0, 0, 0, 0.5);
  }
  
  .portfolio-modal .portfolio-modal-dialog .close-button {
    position: absolute;
    top: 2rem;
    right: 2rem;
  }
  
  .portfolio-modal .portfolio-modal-dialog .close-button i {
    line-height: 38px;
  }
  
  .portfolio-modal .portfolio-modal-dialog h2 {
    font-size: 2rem;
  }
  
  @media (min-width: 768px) {
    .portfolio-modal .portfolio-modal-dialog {
      min-height: 100vh;
      padding: 5rem;
      margin: 3rem calc(3rem - 8px);
    }
    .portfolio-modal .portfolio-modal-dialog h2 {
      font-size: 3rem;
    }
  }
  
  .floating-label-form-group {
    position: relative;
    border-bottom: 1px solid #e9ecef;
  }
  
  .floating-label-form-group input,
  .floating-label-form-group textarea {
    font-size: 1.5em;
    position: relative;
    z-index: 1;
    padding-right: 0;
    padding-left: 0;
    resize: none;
    border: none;
    border-radius: 0;
    background: none;
    box-shadow: none !important;
  }
  
  .floating-label-form-group label {
    font-size: 0.85em;
    line-height: 1.764705882em;
    position: relative;
    z-index: 0;
    top: 2em;
    display: block;
    margin: 0;
    transition: top 0.3s ease, opacity 0.3s ease;
    vertical-align: middle;
    vertical-align: baseline;
    opacity: 0;
  }
  
  .floating-label-form-group:not(:first-child) {
    padding-left: 14px;
    border-left: 1px solid #e9ecef;
  }
  
  .floating-label-form-group-with-value label {
    top: 0;
    opacity: 1;
  }
  
  .floating-label-form-group-with-focus label {
    color: #18BC9C;
  }
  
  form .row:first-child .floating-label-form-group {
    border-top: 1px solid #e9ecef;
  }
  
  .footer {
    padding-top: 5rem;
    padding-bottom: 5rem;
    background-color: #2C3E50;
    color: #fff;
  }
  
  .copyright {
    background-color: #1a252f;
  }
  
  .btn {
    border-width: 2px;
  }
  
  .text-primary {
    color: #18BC9C !important;
  }
  
  .text-secondary {
    color: #2C3E50 !important;
  }
  
  
  .btn-secondary {
    background-color: #2C3E50;
    border-color: #2C3E50;
  }
  
  .btn-secondary:hover, .btn-secondary:focus, .btn-secondary:active {
    background-color: #1a252f;
    border-color: #1a252f;
  }

  .btn-primary {
    background-color: #f48d5c;
    border-color: #f48d5c;
  }

  .btn-primary:hover {
    background-color: #bf6c44;
    border-color: #bf6c44;
  }

  .btn-primary:disabled {
    background-color: #bf6c44;
    border-color: #bf6c44;
  }

  .progress-bar {
    background-color: #f48d5c
  }

  a {
    color: #f48d5c;
  }
  
  a:focus, a:hover, a:active {
    color: #bf6c44;
  }

  
.table td, .table th {
    padding: 0 !important;
    border-width: 0px !important;
}

.bg-secondary {
    background-color: #f2e6e7 !important;
}

#mainNav .navbar-brand {
    color: black;
}

#mainNav .navbar-nav li.nav-item a.nav-link {
    color: #464646;
  }
  
  #mainNav .navbar-nav li.nav-item a.nav-link:hover {
    color: black;
  }
  
  #mainNav .navbar-nav li.nav-item a.nav-link.active {
    color: black;
  }

  .address-list {
    font-size: 80%; 
    opacity: 0.2;
    margin-bottom: 1.5em;
    transition: opacity 0.4s ease;
  }
  .address-list:hover {
    opacity: 0.7;
  }
  
